* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #eff3f7;
  height: 100vh;
}

html,
body,
.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 25px;
  text-shadow: 2px 1px 1px rgba(0, 0, 0, 0.5);
}
.movies {
  padding-top: 40px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px;
  width: 80%;
}

.movies .movie {
  width: 40%;
  background-color: white;
  margin-bottom: 70px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.3);
}

.movie img {
  max-width: 140px;
  margin-right: 20px;
  position: relative;
  top: -20px;
}

.movie h3,
.movie h5 {
  font-weight: 300;
  margin-bottom: 0;
}
.movie h3 {
  font-size: 22px;
}
.movie h5,
.movie .movie__year {
  opacity: 0.8;
}
.movie .movie__genre {
  font-size: 16px;
  opacity: 0.8;
}
.movie .movie__summary {
  opacity: 0.7;
  font-size: 13px;
}
